import store from '@/store/index.js'
// ,,vuex,
export function isLogin() {
	var userObj = store.state.user.userInfo || {};
	console.log(userObj)
	if (userObj.nickName) {
		return true;
	}
	return false;
}
//     
export function checkLogin(fn) {
	// 
	if (!isLogin()) {
		uni.showModal({
			title: '',
			content: '',
			/**
			 * 
			 */
			success: (res) => {
				if (res.confirm) {
					uni.redirectTo({
						url: '/pages/mine/login/login'
					})
				}
			}
		})
	} else {
		fn();
	}
}

export function getDomInfo(name, vm) {
	return new Promise((reslove, reject) => {
		const query = uni.createSelectorQuery().in(vm);
		query
			.select(name)
			.fields({size:true,rect:true},data => {
				reslove(data)
			})
			.exec();
	})
}
