export default {
	userInfo(state) {
		//vuex
		var userObj = state.user.userInfo;
		//  vuex {} false 
		if (userObj.nickName) {
			// console.log('userObj',userObj);
			return userObj;
		} else {
			return {}
		}
		// vuex storage

	},
	/*  */
	runState(state) {
		/*0: 1: 2:*/
		var run_state = state.running.run_state;
		if (run_state === 0) {
			return 0;
		} else if (run_state === 1) {
			return 1
		} else {
			return 2
		}

	},
	/*  */
	runStart(state) {
		var start = state.running.start;
		if (start["latitude"]) {
			return start;
		} else {
			return {}

		}
	},
	getSessionKey(state) {
		let sessionKey = uni.getStorageSync('sessionKey');
		console.log(Boolean(sessionKey))
		if (sessionKey) {
			console.log("sessionKey", sessionKey)
			return sessionKey;
		}
		return ""
	},
	openId(state, getters) {
		let openId = state.user.openId;
		console.log("store-openid", state.user.userInfo)
		//  vuex {} false 
		if (openId) {
			// console.log('userObj',userObj);
			return openId;
		}
		// vuex storage

	},
	home_bg(state) {
		let bg = state.bg.home;
		if (bg) {
			return bg
		}
	},
	appTheme(state) {
		return state.setting.appTheme
	}
}
